import { defineComponent, reactive } from 'vue';
import { useDialog } from '../../src';
export default defineComponent({
    setup: function () {
        var resolve = useDialog().resolve;
        var form = reactive({
            field1: '',
            field2: 'default',
        });
        function submit() {
            resolve(form);
        }
        return {
            form: form,
            submit: submit,
        };
    },
});
