import { defineComponent, h } from 'vue';
import { useDialog } from '../../src';
export default defineComponent({
    props: {
        text: {
            type: String,
            default: 'Simple info dialog',
        },
    },
    setup: function (props) {
        var resolve = useDialog().resolve;
        return function () {
            return h('div', [
                h('div', props.text),
                h('button', {
                    onClick: function () {
                        resolve();
                    },
                }, ['OK']),
            ]);
        };
    },
});
